@import '../base.scss';

.c-article-diaries-slider {

  &__inner {
    padding-bottom: 2.5rem;
  }

  &__content {
    padding-top: 3.9375rem;
    border-top: 0.125rem solid rgba(0, 72, 81, 0.2);
    @include t-gutters(true);
    margin: 0 auto 4.625rem;
    max-width: 35rem;
    text-align: center;
    width: 101%;
  }

  &__title {
    margin-bottom: 0.875rem;
  }

  &__buttons {
    display: none;
  }

  &__article-wrapper {
    text-align: center;
    padding-bottom: 0.375rem;

    &:hover {
      .c-article-diaries-slider__btn::before {
        width: 100%;
      }
    }
  }

  &__image {
    opacity: 0.95;

    &-container {
      padding-bottom: 100%;
    }
  }

  &__article-title {
    text-align: center;
    margin-top: 1rem;
    font-weight: 400;
    margin-bottom: 0.5rem;
  }

  @include t-media-query($s-medium-up) {
    max-width: 125rem;
    margin-left: auto;
    margin-right: auto;

    &__inner {
      padding-top: 1.25rem;
    }

    &__content {
      width: 86%;
      padding: 7.5rem 0 0;
      margin-bottom: 3.875rem;
    }

    &__article-title {
      margin-top: 1.5rem;
      margin-bottom: 0.8125rem;
    }

    &__articles-container {
      position: relative;
    }

    &__buttons {
      display: block;
      width: 100%;
      @include t-absolute-center-y();
      top: 31%;
    }

    &__button {
      cursor: pointer;
      position: absolute;
      width: 4rem;
    }

    &__articles-wrapper {
      max-width: 95.5rem;
      margin: 0 auto;
      width: 80%;
    }

    &__button.is-prev {
      left: 3.5%;
      transform: rotate(180deg);
    }

    &__button.is-next {
      right: 3.5%;
    }
  }

  @include t-media-query($s-large-up) {
    &__button.is-prev {
      left: 5.5%;
      transform: rotate(180deg);
    }

    &__button.is-next {
      right: 5.5%;
    }
  }


}